<template>
  <div class="comment-detail-container">
    
    <div class="main-message">
      <MessageList
        :messageList="1"
        :isShowReply="false"
      />
    </div>

    <div class="split-div-1" />

    <div class="reply-message">
      <MessageList
        :messageList="3"
        :isShowReply="false"
        :isShowCommentLink="false"
      />
    </div>

    <WriteMessageBox class="write-message-box" />

    <div class="bottom-block" />

  </div>
</template>

<script>
import MessageList from '@/components/message/MessageList.vue';
import WriteMessageBox from '../../components/message/WriteMessageBox.vue';

export default {
  components: {
    MessageList,
    WriteMessageBox
  },

  created() {
    document.title = '留言详情';
  },
}
</script>

<style lang="scss" scoped>
.comment-detail-container {
  width: 100%;
  background-color: #fff;

  .main-message, .reply-message {
    width: 100%;
    box-sizing: border-box;
    padding: 20px 20px 0;
  }

  .split-div-1 {
    width: 100%;
    height: 10px;
    background: rgba(246,246,246,1);
  }

  .write-message-box {
    width: 100%;
    position: fixed;
    bottom: 0;
    background-color: #fff;
  }

  .bottom-block {
    width: 100%;
    height: 55px;
  }
}
</style>


